import popupPay from '../../components/popupPay.vue';
import interfaces from '../../utils/interface.js'





export default {
	data() {
		return {
			orderNav: ['全部', '待付款', '代发货', '待收货', '已完成'],
			showOrderIndex: 0,
			showOrder: [], //显示订单
			orderLists: [], //全部订单
			dfk_order: [], //待付款订单
			dfh_order: [], //代发货订单
			dsh_order: [], //待收货订单
			end_order: [], //已完成订单
			loadingText: "正在加载....",
			loading: true,
			showPay: false, //隐藏支付
			sum: '', //总价
			order_num :'' ,//订单编号

		}
	},
	components: {
		popupPay
	},
	onShow() {
		
	},
	methods: {
		showOrderNav(index) {
			this.showOrderIndex = index;
			switch (index) {
				case 0:
					this.showOrder = this.orderLists;
					break;
				case 1:
					this.showOrder = this.dfk_order;
					break;
				case 2:
					this.showOrder = this.dfh_order;
					break;
				case 3:
					this.showOrder = this.dsh_order;
					break;
				case 4:
					this.showOrder = this.end_order;
					break;
			}
		},
		async initData() {
			this.request({
				url: interfaces.all_order, //获取个人所有订单
				method: "POST",
				success: ((res) => {
					// console.log(res)
					if (res.error_code == 0) {
						this.orderLists = res.data;
						this.showOrder = res.data;
						res.data.forEach((item) => {
						  item.time =  this.timestampToTime(item.time)
						})
						this.dfk_order = res.data.filter((item) => {
							return item.state_name === '待付款';
						})
						this.dfh_order = res.data.filter((item) => {
							return item.state_name === '待发货';
						})
						this.dsh_order = res.data.filter((item) => {
							return item.state_name === '待收货';
						})
						this.end_order = res.data.filter((item) => {
							return item.state_name === '已完成';
						})
						this.loading = false;
					}
				})
			});
			// let data = await this.$api.json('data');
			// this.orderLists = data.orderLists;
			// this.showOrder = data.orderLists;























			

		},
		//跳转查看商品
		navTo(item) {
			if (item.state_name == '待付款') {
				uni.navigateTo({
					url: '../pay/toPay?goodsInfo=' + JSON.stringify(item)
				})
			} else if (item.state_name == '待收货' || item.state_name == '待发货') {
				uni.navigateTo({
					url: '../pay/endOrder?goodsInfo=' + JSON.stringify(item)
				})
			} else if (item.state_name == '已完成') {
				uni.navigateTo({
					url: '../pay/endOrder?goodsInfo=' + JSON.stringify(item)
				})
			}
		}, //售后
		toSale(item) {
			uni.navigateTo({
				url: './base/afterSale'
			})
		},
		toComment(item) { //精确跳转
			if (item.state_name == '已完成') {
				uni.navigateTo({
					url: './base/orderComment?goodsInfo=' + JSON.stringify(item)
				})
				return;
			} else if (item.state_name == '待付款') { //直接付款
				this.order_num = item.order_num;
				this.sum = item.all_price;
				this.showPay = true;
				this.$refs.Series.Payment() //触发子组件方法(获取支付通道)
				return;
			} else if (item.iconTitle == '提醒发货') {
				uni.showLoading({
					title: "发送中..."
				})
				setTimeout(() => {
					uni.hideLoading()
					setTimeout(() => {
						uni.showToast({
							icon: "success",
							title: "已成功提示商家",
						})
					}, 200)
				}, 1000)
			} else if (item.iconTitle == '删除订单') {
				uni.showModal({
					title: '提示',
					content: '确定要删除订单吗？',
					cancelText: '否',
					confirmText: '是',
					success: res => {
						// if (res.confirm) {
						// 	this.imgList.splice(e.currentTarget.dataset.index, 1)
						// }
					}
				})
			} else if (item.iconTitle == '确认收货') {
				uni.showModal({
					title: '确认收货',
					content: '确定订单吗？',
					cancelText: '否',
					confirmText: '是',
					success: res => {
						setTimeout(() => {
							uni.showToast({
								icon: "success",
								title: "订单已完成",
							})
						}, 200)
						// if (res.confirm) {
						// 	this.imgList.splice(e.currentTarget.dataset.index, 1)
						// }
					}
				})
			}

		},
		setPay(show) { //传入子组件的方法
			this.showPay = show;
		},
		timestampToTime(cjsj) {
			let time = cjsj.toString()
			if(time.length == 10){
				time = time * 1000
			}
			var date = new Date(time) //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			var Y = date.getFullYear() + '-'
			var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
			var D = date.getDate() + ' '
			var h = date.getHours() + ':'
			var m = date.getMinutes() + ':'
			var s = date.getSeconds()
			return Y + M + D + h + m + s
		},
	},
	onLoad() {
		this.initData();
	}
}
